<template>
  <div>
    <van-nav-bar
      title=""
      left-text=""
      left-arrow
      @click-left="onClickLeft"
    />
    <div class="wrap">
      <h2 class="title">
        <span class="sub-title" @click="getText"></span>
      </h2>
      <div class="img-box">
        <van-loading type="spinner" color="#FF9900" v-if="loading">
          ......
        </van-loading>
        <template v-else>
          <p>{{ nickname }}</p>
          <canvas id="mycanvas"></canvas>
          <p>
            {{ desc }}
          </p>
          <div id="qrcode"></div>
        </template>
      </div>
    </div>
    <div class="poster">
      <van-button type="warning" @click="create"></van-button>
    </div>
    <van-overlay v-if="show" :show="true" @click="down">
      <div id="poster">
        <img :src="canvasUrl" alt="" class="img" />
        <div class="down-btn">
          <van-button plain type="primary" @click="down"></van-button>
        </div>
      </div>
    </van-overlay>
  </div>
</template>

<script>
import { HOST } from '@/utils/constant';
import html2canvas from 'html2canvas';
import axios from 'axios';
export default {
  data() {
    return {
      loading: false,
      imgUrl: '',
      desc: '',
      show: false,
      canvasUrl: '',
      nickname: '',
    };
  },
  computed: {},
  created() {
    this.getResult();
    this.getText();
    this.nickname = localStorage.getItem('nickname');
  },
  methods: {
    loadImage() {
      var img = new Image();
      img.setAttribute('crossorigin', 'anonymous');
      img.onload = function () {
        const mycanvas = document.getElementById('mycanvas');
        mycanvas.width = img.width;
        mycanvas.height = img.height;
        const ctx = mycanvas.getContext('2d');
        ctx.drawImage(img, 0, 0, img.width, img.height);
      };
      img.src = this.imgUrl;
    },
    down() {
      const a = document.createElement('a');
      a.setAttribute('download', 'poster.png');
      a.href = this.canvasUrl;
      a.click();
      this.show = false;
    },
    createQr() {
      /* eslint-disable */
      new QRCode(document.getElementById('qrcode'), {
        text: location.href,
        width: 128,
        height: 128,
        colorDark: '#000000',
        colorLight: '#ffffff',
        correctLevel: QRCode.CorrectLevel.H,
      });
    },
    create() {
      this.show = true;
      this.$nextTick(() => {
        html2canvas(document.querySelector('.img-box')).then((canvas) => {
          this.canvasUrl = canvas.toDataURL('image/png');
        });
      });
    },
    getImg({ requestId, taskId }) {
      axios
        .post(`${HOST}/api/subjectInfo/getImgMessage`, {
          requestId,
          taskId,
        })
        .then((response) => {
          const {
            data: { code, data },
          } = response;
          if (code === '200' && data.img) {
            this.imgUrl = data.img;
            this.loading = false;
            this.createQr();
            this.loadImage();
            clearTimeout(this.timer);
          }
        })
        .catch((error) => {
          this.loading = false;
          console.log(error);
        });
      this.loop({ requestId, taskId });
    },
    loop(data) {
      if (this.imgUrl) return;
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        this.getImg(data);
      }, 3000);
    },

    getResult() {
      const data = this.$store.state.anwser;
      if (!data || !data.length) {
        return this.onClickLeft();
      }
      this.loading = true;

      axios
        .post(
          `${HOST}/api/subjectInfo/submitSubjectMessage`,
          this.$store.state.anwser
        )
        .then((response) => {
          const {
            data: { code, data },
          } = response;
          if (code === '200') {
            const { requestId, taskId } = data;
            if (requestId && taskId) {
              this.getImg(data);
            } else{
              // this.getResult();
            }
          } else {
            this.loading = false;
          }
        })
        .catch((error) => {
          this.loading = false;
          console.log(error);
        });
    },

    getText() {
      const data = this.$store.state.anwser;
      if (!data || !data.length) {
        return this.onClickLeft();
      }
      axios
        .post(`//10.181.168.166:8081/api/subjectInfo/getCopyWriting`, {
          text: data
            .reduce((all, item) => {
              return all.concat(item.answers.map((i) => i.optionDescription));
            }, [])
            .join(','),
        })
        .then((response) => {
          const {
            data: { code, data },
          } = response;
          if (code === '200') {
            this.desc = data.result;
          }
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    onClickLeft() {
      this.$router.back();
    },
  },
};
</script>

<style lang="less" scoped>
@import './index.less';
</style>
